@import url(http://fonts.cdnfonts.com/css/circles-new);
@import url(http://fonts.cdnfonts.com/css/stencilia);
@import url(http://fonts.cdnfonts.com/css/stencilia);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  display: grid;
  grid-template-columns: 71vh 10vh 45vh;
  position: fixed;
}

.logo {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  font-family: 'Circles_New', sans-serif;
  font-size: 8vh;
  grid-column: 2;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  line-height: 10px;
  text-shadow: #9aff00 1.5px 1px 1px;
}

.nav {
  grid-column: 3;
  font-size: 3vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: start;
          justify-content: start;
  -webkit-align-items: center;
          align-items: center;
}

.nav a {
  text-decoration: none;
  color: white;
  padding: 2vh;
}
body {
  margin: 0 auto;
}

.circlesContainer{
  display: grid;
  grid-template-columns: 20vh 20vh;
  grid-template-rows: 20vh 20vh;
  background-color: black;
  width: 60vh;
  height: 45vh;
  padding: 20px;
  left: 10vh;
  position: relative;
}

.avaiable, .date {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  position: relative;
  color: white;
}

.containerAvaiable {
  position: relative;
  grid-column: 1;
  grid-row: 2;
  left: 10vh;
  z-index: 10;
  font-family: 'Stencilia-A', sans-serif;
  font-size: 4vw;
  text-align: center;
  height: 35vh;
  width: 34.7vh;
  bottom: 10vh;
  text-shadow: #9aff00 1px 0px 1px;
  -webkit-animation: borderEffect forwards 1.3s 1;
          animation: borderEffect forwards 1.3s 1;
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
  transition: 1s all;
}

.avaiable {
  grid-column: 1;
  grid-row: 2;
  left: 37%;
  z-index: 10;
  font-family: 'Stencilia-A', sans-serif;
  font-size: 5vh;
  text-align: center;
  height: 35vh;
  width: 34.7vh;
  bottom: 60%;
  text-shadow: #9aff00 1px 0px 1px;
}

@-webkit-keyframes borderEffect {
  0% {
    width: 0vh;
    height: 0vh;
    border-top: 0px solid #9aff00;
    border-radius: 0%;
    border-bottom-left-radius: 0%;
  }
  33% {
    width: 0vh;
    height: 30vh;
    border-left: 1px solid #9aff00;
    border-radius: 0%;
  }
  66% {
    width: 30vh;
    height: 30vh;
    border-left: 1px solid #9aff00;
    border-bottom: 1px solid #9aff00;
    border-radius: 0%;
  }
  100% {
    width: 30vh;
    height: 30vh;
    border-left: 1px solid #9aff00;
    border-bottom: 1px solid #9aff00;
    border-right: 1px solid #9aff00;
    border-radius: 50%;
    border-bottom-left-radius: 0%;
  }
}

@keyframes borderEffect {
  0% {
    width: 0vh;
    height: 0vh;
    border-top: 0px solid #9aff00;
    border-radius: 0%;
    border-bottom-left-radius: 0%;
  }
  33% {
    width: 0vh;
    height: 30vh;
    border-left: 1px solid #9aff00;
    border-radius: 0%;
  }
  66% {
    width: 30vh;
    height: 30vh;
    border-left: 1px solid #9aff00;
    border-bottom: 1px solid #9aff00;
    border-radius: 0%;
  }
  100% {
    width: 30vh;
    height: 30vh;
    border-left: 1px solid #9aff00;
    border-bottom: 1px solid #9aff00;
    border-right: 1px solid #9aff00;
    border-radius: 50%;
    border-bottom-left-radius: 0%;
  }
}

.date {
  grid-column: 2;
  grid-row: 1;
  z-index: 30;
  font-family: 'Stencilia-A', sans-serif;
  font-size: 4vh;
  text-align: center;
  box-shadow: none;
  position: relative;
  height: 20vh;
  background-color: black;
  border-bottom-left-radius: 100%;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: baseline;
          align-items: baseline;
  display: -webkit-flex;
  display: flex;
  right: 9vh;
  text-shadow: #9aff00 1px 0px 1px;
  -webkit-animation: borderEffectDois forwards 1s 1;
          animation: borderEffectDois forwards 1s 1;
  -webkit-animation-delay: 0.7s;
          animation-delay: 0.7s;
  transition: 2s all;
}

@-webkit-keyframes borderEffectDois {
  0% {
    width: 0;
    height: 0;
    border-top: 0px solid #9aff00;
    border-right: 0px solid #9aff00;
  }
  30% {
    border-top-right-radius: 1vh;
  }
  50% {
    width: 40vh;
    height: 0;
    border-top-right-radius: 2.5vh;
  }
  75% {
    border-top-right-radius: 4vh;
  }
  100% {
    height: 21vh;
    width: 40vh;
    border: 1px solid #9aff00;
    border-bottom: none;
    border-left: none;
    border-top-right-radius: 5vh;
  }
}

@keyframes borderEffectDois {
  0% {
    width: 0;
    height: 0;
    border-top: 0px solid #9aff00;
    border-right: 0px solid #9aff00;
  }
  30% {
    border-top-right-radius: 1vh;
  }
  50% {
    width: 40vh;
    height: 0;
    border-top-right-radius: 2.5vh;
  }
  75% {
    border-top-right-radius: 4vh;
  }
  100% {
    height: 21vh;
    width: 40vh;
    border: 1px solid #9aff00;
    border-bottom: none;
    border-left: none;
    border-top-right-radius: 5vh;
  }
}

.month {
  position: relative;
  font-size: 8vh;
  grid-column: 2;
  grid-row: 1;
  z-index: 30;
  font-family: 'Stencilia-A', sans-serif;
  font-size: 6vh;
  text-align: center;
  box-shadow: none;
  position: relative;
  height: 20vh;
  width: 50vh;
  left: -12vh;
  top: 1vh;
}

.day {
  grid-column: 2;
  grid-row: 1;
  z-index: 30;
  font-family: 'Stencilia-A', sans-serif;
  font-size: 4vh;
  text-align: center;
  box-shadow: none;
  position: relative;
  height: 20vh;
  width: 50vh;
  top: 8vh;
  position: relative;
  font-size: 7vh;
  right: 5vh;
}

body {
  background-color: black;
}

.firstPage {
  margin: 5vh;
  display: grid;
  grid-template-rows: 7vh 90vh;
  background-color: black;
  color: white;
}

.header {
  width: 100vw;
  position: fixed;
  transition: all 1s;
  grid-row: 1;
}

.hero {
  grid-row-start: 2;
  display: grid;
  grid-template-columns: 30vw 37vw 30vw;
  grid-template-rows: 45vh 50vh;
  font-family: 'Stencilia-A', sans-serif;
  grid-row-end: 3;
}

.helloContainer {
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row: 1;
  font-size: 7vw;
  margin-left: 5vh;
  text-shadow: #9aff00 3px 0px 2px;
  z-index: 20;
  transition: 1s all;
}

.cargo {
  grid-column: 3;
  grid-row: 1;
  font-size: 3vw;
  text-align: right;
  margin-right: 5vh;
  text-shadow: #9aff00 1.5px 0px 1px;
}

.text {
  grid-column: 3;
  grid-row: 2;
  font-size: 1.2vw;
  text-align: justify;
  text-shadow: #9aff00 1px 0 1px;
  align-self: center;
  padding: 5vh;
}

.description {
  grid-column: 3;
  grid-row: 2;
  align-self: center;
  border-bottom-left-radius: 5vh;
  -webkit-animation: borderTeste 1s 1 forwards;
          animation: borderTeste 1s 1 forwards;
  -webkit-animation-delay: 2.1s;
          animation-delay: 2.1s;
}

@-webkit-keyframes borderTeste {
  0% {
    width: 0;
    height: 0;
    border-color: transparent;
  }
  50% {
    width: 0;
    height: 25vh;
    border-left: #9aff00 1px solid;
  }
  100% {
    width: 65vh;
    height: 25vh;
    border-left: #9aff00 1px solid;
    border-bottom-left-radius: 5vh;
    border-bottom: 1px solid #9aff00 ;
  }
}

@keyframes borderTeste {
  0% {
    width: 0;
    height: 0;
    border-color: transparent;
  }
  50% {
    width: 0;
    height: 25vh;
    border-left: #9aff00 1px solid;
  }
  100% {
    width: 65vh;
    height: 25vh;
    border-left: #9aff00 1px solid;
    border-bottom-left-radius: 5vh;
    border-bottom: 1px solid #9aff00 ;
  }
}

