    border-color: transparent;
  }
  50% {
    width: 0;
    height: 21vh;
    border-left: #9aff00 1px solid;
  }
  100% {
    width: 55vh;
    height: 21vh;
    border-left: #9aff00 1px solid;
    border-bottom-left-radius: 5vh;
    border-bottom: 1px solid #9aff00 ;
  }
}

@keyframes borderTeste2 {
  0% {
    width: 0;
    height: 0;
    border-color: transparent;
  }
  50% {
    width: 0;
    height: 21vh;
    border-left: #9aff00 1px solid;
  }
  100% {
    width: 55vh;
    height: 21vh;
    border-left: #9aff00 1px solid;
    border-bottom-left-radius: 5vh;
    border-bottom: 1px solid #9aff00 ;
  }
}

@-webkit-keyframes borderTeste3 {
  0% {
    width: 0;
    height: 0;
    border-color: transparent;
  }
  50% {
    width: 0;
