@media (max-width: 850px) {
  .circlesContainer {
    width: 0;
    height: 0;
    padding: 0;
    left: 0;
  }
}

@media (max-width: 650px) {
  .circlesContainer {
    position: absolute;
    top: 25vh;
    left: 10vh;
  }
  .description {
    grid-column: 2;
    grid-row: 2;
    -webkit-animation: borderTeste2 1s 1 forwards;
            animation: borderTeste2 1s 1 forwards;
    position: relative;
    left: -10vh;
  }
  .text {
    grid-column: 2;
    grid-row: 2;
    font-size: 2.5vw;
    width: 50vw;
    padding: 5vh;
    position: relative;
    left: -10vh;
  }
}

@media (max-width: 450px) {
  .helloContainer {
    font-size: 10vw;
  }
  .header {
    -webkit-transform: translate(0);
            transform: translate(0);
    grid-template-columns: auto;
  }
  .circlesContainer {
    left: -1vh;
    top: 39vh;
  }
  .description {
    left: -13vh;
    top: 10vh;
    -webkit-animation: borderTeste3 1s 1 forwards;
            animation: borderTeste3 1s 1 forwards;
  }
  .text {
    left: -13vh;
    top: 9vh;
    padding-bottom: 2vh;
  }
}
